import React from 'react';
import Textfit from 'react-textfit';
import './Hat.css';

class Portrait extends React.Component {
    constructor(props) {
      super(props);
      this.state = {};
    }
    
    render() {
      return (
        <div className="line">
            <Textfit mode='single' throttle='1000'>                                                                                 |-------------|    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |     __      |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |    |___&#92;    |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |   &#47;     &#92;   |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |   |     |   |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |   |_  _ &#47;|  |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |   |&#62;| &#60; ||  |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |   &#92;&#47;()&#92; &#47;   |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |    |^^  |   |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |    &#92;-- &#47;    |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |    |&#92;_&#47;|&#92;   |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |  _&#47;&#92;   | &#92;_ |    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 | &#47;   &#92;  &#47;   &#92;|    </Textfit>
            <Textfit mode='single' throttle='1000'>                                                                                 |-------------|    </Textfit>
        </div>
      );
    }
  }
  
  export default Portrait;